@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  width: 100%;
  height: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto', sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100vh;
  color: #333333;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.clicker {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100px;
  height: 400px;
}

h2 {
  margin: 0;
  padding: 0;
  font-size: 42px;
  cursor: default;
  text-transform: uppercase;
}

span {
  font-weight: 700;
  cursor: default;
  font-size: 32px;
}

button {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #dddddd;
  border-radius: 3px;
  outline: none;
  width: 30px;
  height: 30px;
  margin: 10px 0;
}

input {
  cursor: default;
  text-transform: lowercase;
  font-weight: 100;
  font-style: italic;
  padding: 10px 0;
  font-size: 20px;
  text-align: center;
  border: none;
  outline: none;
  background-color: #ffffff;
}
